<template>
  <div class="main-panel">
    <div v-if="isDrawNumber" class="div-row">
      <div class="div-center item" v-bind:key="number + 'IndexDrawIndex' + id" v-for="(item, number) in index">
        <div>{{ number + 1 }}</div>
      </div>
    </div>
    <div class="div-row">
      <div class="div-center item-element" v-bind:key="number + 'IndexDrawIndex' + id"
           v-for="(item, number) in index">
        <div>{{ item }}</div>
      </div>
      <div style="width: 100px;" v-if="title !== undefined" class="div-center item-element"><div>{{ title }}</div></div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    index: {
      type: Array,
      default: () => []
    },
    isDrawNumber: {
      type: Boolean,
      default: () => false
    },
    title: {
      type: String,
      default: () => undefined
    }
  },
  name: "IndexDraw",
  data() {
    return {
      id: (function randomString(i) {
        let rnd = '';
        while (rnd.length < i)
          rnd += Math.random().toString(36).substring(2);
        return rnd.substring(0, i);
      })(10)
    }
  }
}
</script>

<style scoped>
.item {
  width: 62px;
}

.item-element {
  background-color: antiquewhite;
  border: black solid 1px;
  width: 60px;
}

.item-element:hover {
  background-color: #1295ea;
  cursor: pointer;
}

.div-center {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}

.div-row {
  display: flex;
  flex-direction: row;
}

.main-panel {
  display: flex;
  flex-direction: column;
}
</style>
