<template>
  <div style="width: 100%; height: 100%; display: flex; flex-direction: column">
    <div style="flex: 0 0 50px; display: flex; flex-direction: row;">
      <span>R= </span>
      <div style="flex: 0 0 3px;"/>
      <input type="number" style="width: 40px;  height: 15px;" v-model="R"/>
      <div style="flex: 0 0 15px;"/>
      <span>Z= </span>
      <div style="flex: 0 0 3px;"/>
      <input type="number" style="width: 40px;  height: 15px;" v-model="Z"/>
      <div style="flex: 0 0 15px;"/>
      <VisualMatrix v-model="Z" :numberProps="Z"/>
    </div>
    <div>
      <indexDraw :index="numberModule(Z)" :isDrawNumber="true" :title="'Признаки Z'"></indexDraw>
      <indexDraw :index="omega" :title="'Веса W'"></indexDraw>
    </div>
    <button class="button" @click="teaching">Обучить нейрон</button>
    <div style="flex: 0 0 300px; display: flex; flex-direction: row">
      <div
          style="flex: 0 0 300px; display: flex; flex-direction: column; border-right: black solid 4px; padding-right: 3px; margin-right: 3px">
        <div style="flex: 0 0 30px; display: flex; flex-direction: row;">
          <span>Размер обучающей выборки - </span>
          <div style="flex: 0 0 3px;"/>
          <input type="number" style="width: 40px;  height: 15px;" v-model="sizeTeaching"/>
        </div>
        <div style="flex: 0 0 30px; display: flex; flex-direction: row;">
          <span>Размер проверяющей выборки - </span>
          <div style="flex: 0 0 3px;"/>
          <input type="number" style="width: 40px;  height: 15px;" v-model="sizeTesting"/>
        </div>
        <div style="flex: 1 1 1px">
          <line-chart style="height: 100%; width: 100%;" :chartData="chartdata" :options="{
        responsive: true,
        plugins: {
          legend: {
            position: 'top',
          },
          title: {
            display: true,
            text: 'Процент обучения персептрона',
          },
        }
      }"/>
        </div>
      </div>
      <div style="flex: 1 1 1px; display: flex; flex-direction: column;">
        <div v-bind:key="item" v-for="item in output">
          Процент правильности: {{ item.result.toFixed(2) }}%
          <indexDraw :index="item.index"></indexDraw>
        </div>
      </div>
    </div>
    <div style="flex: 0 0 300px;">
      <line-chart :chartData="chartDataOmegaChange"
                  :options="{
        responsive: true,
        plugins: {
          legend: {
            position: 'top',
          },
          title: {
            display: true,
            text: 'Результат обучения',
          },
        }
      }"/>
    </div>
    <div style="flex: 0 0 300px;">
      <line-chart :chartData="chartDataTest"
                  :options="{
        responsive: true,
        plugins: {
          legend: {
            position: 'top',
          },
          title: {
            display: true,
            text: 'Результат c помехами',
          },
        }
      }"/>
    </div>
  </div>
</template>

<script>
import numberModule from "@/components/neuromod/Laboratory/modules/NumberModule";
import indexDraw from './IndexDraw'
import VisualMatrix from "@/components/neuromod/VisualComponents/VisualMatrix";
import {teachingNero, getData, checkNeuron} from "@/components/neuromod/Laboratory/modules/NeroMod";


import {LineChart} from 'vue-chart-3';
import {Chart, registerables} from "chart.js";

Chart.register(...registerables);


export default {
  components: {
    indexDraw,
    VisualMatrix,
    'line-chart': LineChart
  },
  name: "InputForm",
  data() {
    return {
      R: 30,
      Z: 2,
      omega: this.randomArray(15),
      sizeTeaching: 10,
      sizeTesting: 100,
      output: []
    }
  },
  computed: {
    chartDataTest() {
      let result = [], node = [];
      if (this.output.length === 0) {
        return undefined;
      }
      let data = getData(this.sizeTeaching, this.sizeTesting);
      result.push(checkNeuron(data.testing, this.output[this.output.length - 1].index, this.R, this.Z, 0));
      node.push(0)
      for (let g = 0; g < this.output[this.output.length - 1].index.length; g++) {
        result.push(checkNeuron(data.testing, this.output[this.output.length - 1].index, this.R, this.Z, g + 1));
        node.push(g + 1)
      }
      return {
        labels: node,
        datasets: [{
          label: 'Процент правильности определения с помехами',
          backgroundColor: '#1295ea',
          data: result
        }]
      }
    },
    chartDataOmegaChange() {
      let result = [], result2 = [], node = [];
      if (this.output.length === 0) {
        return undefined;
      }
      for (let g = 0; g < this.output[this.output.length - 1].index.length; g++) {
        result.push(this.output[this.output.length - 1].index[g])
        node.push(g + 1)
        result2.push(this.omega[g])
      }
      return {
        labels: node,
        datasets: [{
          label: 'Изначальные веса',
          backgroundColor: '#f87979',
          data: result2
        }, {
          label: 'После обучения',
          backgroundColor: '#1295ea',
          data: result
        }]
      }
    },
    chartdata() {
      let result = [0], node = [0];
      for (let g = 0; g < this.output.length; g++) {
        result.push(this.output[g].result)
        node.push(g + 1)
      }
      return {
        labels: node,
        datasets: [{
          label: '',
          backgroundColor: '#f87979',
          data: result
        }]
      }
    }
  },
  methods: {
    numberModule(number) {
      return numberModule(number);
    },
    randomArray(size) {
      let out = [];
      for (let g = 0; g < size; g++) {
        let array = new Uint32Array(1);
        window.crypto.getRandomValues(array);
        out.push(array[0] % 10);
      }
      return out;
    },
    async teaching() {
      let data = getData(this.sizeTeaching, this.sizeTesting);
      let index = Array.from(this.omega);
      let needLoop = true;
      this.output = [];
      while (needLoop) {
        let result = await teachingNero(index, data.teaching, data.testing, this.R, this.Z);
        this.output.push(result)
        index = Array.from(result.index)
        await new Promise(resolve => setTimeout(() => resolve(), 50))
        if (!result.haveChange || result.result > 99) {
          needLoop = false;
        }
      }
    }
  }
}
</script>

<style scoped>
.button {
  padding: 3px;
  margin: 3px;
  cursor: pointer;
}
</style>
